.faculty-user-guide-header {
  width: 100%;
  z-index: 3;
  @include breakpoint($small-screen-down) {
    .user-guide-heading {
      font-size: 1.5em;
    }
    .smaller {
      padding: 0;
    }
  }
}

.faculty-user-guide {
  .text-purple-bold {
    color: $purple;
    font-weight: $bold;
  }
  @include breakpoint($small-screen-up) {
  padding-top: 70px;
  }
}

.faculty-user-guide-menu {
  padding-top: 7em;
  padding-bottom: 1em;
  text-align: center;
  @media(min-width: 471px) and (max-width: 767px) {
    padding-top: 5em;
  }
  @include breakpoint($small-screen-up) {
    @include span(4);
    max-width: 280px;
    padding: 0;
    position: fixed;
    text-align: left;
  }
}

.faculty-user-guide-content {
  margin-top: -1.5em;
  @include breakpoint($small-screen-up) {
    @include span(8);
    @include last;
  }
  section.selected {
    top: 10px;
    position: relative;
    z-index: -1;
    @media(min-width: 471px) {

    }
    @include breakpoint($small-screen-up) {
      margin-top: 12em;
    }
  }

  section {
    padding-top: $base-spacing;
  }
  h3 {
    margin-bottom: .4em;
  }
}

.back-to-top-arrow {
  background: $white-gray;
  border-radius: 4px 0 0 4px;
  display: none;
  padding: 4px;
  position: fixed;
  bottom: 0;
  right: 0;
  width:47px;
  @include breakpoint($medium-screen-up) {
    background: transparent;
    bottom: 10px;
    padding: 0;
    right: 5px;
    width:39px;
  }
  @include breakpoint($large-screen-up) {
    right: 15px;
  }
}

@include breakpoint($small-screen-up) {
.js-top-100 {
  top: 100px;
 }
}
