//  base/_icons.scss

// Icons  (Background Images)
//
// Styleguide 2.6

// Base Background icon class
//
//  ```icon``` class sets up basic styles for icons. Must always be used with icon classes
//  As these are background image icons they must always have accompanying text for accessibility.
//
// markup:
// <i class="icon">Base icon<i>
//
// Styleguide 2.6.1

.icon::before {
  background-repeat: no-repeat;
  content: " ";
  display: inline-block;
  margin: 0.5em 0.5em 0.5em 0;
  vertical-align: middle;
}
.close-icon {
  cursor: pointer;
  position: absolute;
  top: 9px;
  right: 1%;
}

