//Loading icon
//
// markup:
// <i class="icon loading-icon"><i>
//
// Styleguide 2.6.16
@keyframes spinning-icon {
  100% {
    transform: rotate(360deg);
  }
}
.loading-icon::before {
  @include retina-sprite ($loading-icon-group);
  animation: spinning-icon 2.5s linear infinite;
  margin: 2em 0 3em;
}

