// Image Icons
//
// Styleguide 2.7

// Green checkmark icon
//
//
// markup:
// <img src="assets/images/green-checkmark_2x.png" class="td-icon-right green-checkmark-icon" alt="OK">
//
// Styleguide 2.7.1
.green-checkmark-icon {
  height: 12px;
  width: 16px;
}

// Attention icon
//
// markup:
// <img src="assets/images/attention_2x.png" class="td-icon-right attention-icon" alt="attention!">
//
// Styleguide 2.7.2
.attention-icon {
  height: 18px;
  vertical-align: text-bottom;
  width: 18px;
}

// Attention icon
//
// markup:
// <img src="assets/images/attention_2x.png" class="td-icon-right attention-icon" alt="attention!">
%table-li-icon {
  height: 18px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 18px;
}

// create placeholder for table icon styles
td,
li {
  &.name-with-pay-alert {
    padding-right: 26px !important;
    position: relative;
  }
  .self-pay-alert-table-icon {
    @extend %table-li-icon;
    right: 0.5em;
  }
}
td,
li {
  &.duplicate-student {
    padding-right: 32px !important;
    position: relative;
  }
  .duplicate-student-table-icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0.5em;
  }
}
td,
li {
  img.multiple-icons {
    &:first-of-type {
      @extend %table-li-icon;
      right: 33px;
    }
    &:only-of-type,
    &:last-of-type {
      @extend %table-li-icon;
      right: 0.5em;
    }
  }
}
table + .inline-icon-container > div {
  margin-top: $base-spacing/3;
}
.inline-icon {
  margin-right: 0.25em;
}

