.reporting-links-grid {
  text-align: center;
  @include breakpoint($medium-screen-up) {
    text-align: left;
    @include layout(after);
    @include span(6);
    &:last-child {
      @include last;
    }
  }
}

.client-simulation-links {
  @include breakpoint($medium-screen-down) {
    margin-top: 2em;
    .base-font-color {
      margin-bottom: $small-spacing;
      text-align: center;
    }
    ul > li {
      padding-left: 0;
      text-indent: 0;
      a {
        font-weight: bold;
      }
    }
  }
}
