//  @section
//  modules/_section.scss

// Sections
//
//  Sections contain sections of content with padding on the top and bottom.
//  Colored sections only add a background color. They should be used with another section class that adds padding.
//
//  Note: The ```<section>``` element is not meant to be used for styling per the spec--
//  see http://html5doctor.com/avoiding-common-html5-mistakes/
//  and http://www.w3.org/TR/html5/sections.html#the-section-element for more info
//  "The section element is not a generic container element.
//   When an element is needed only for styling purposes or as a convenience for scripting,
//   authors are encouraged to use the div element instead."
//
//
//Real color names are used here as background color is not tied
//to any action or content, and using a real color is easier to remember
// while coding than "brand-complimentary" for instance.
//
//If the design changes drastically and the brand color changes
//from, let's say, blue to green, just do a global
//find/replace - that's what text editors are for.
//
// Styleguide 4.3


// Thin section
//
// markup:
// <div class="thin-section">
// <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b>
// report from the <a [routerLink]="['/Reports']">View Reports</a> page.</p>
// </div>
//
// .thin-section    - Section with half the padding of ```.section-medium-thin``` and one quarter of base ```.section```.
//  This is the only section class that SHOULD NOT be used with base  ```.section``` class as media query would increase its padding on small screens
//
// Styleguide 4.3.1


.thin-section {
  padding-bottom: $base-spacing/2;
  padding-top: $base-spacing/2;
}


// Section styles
//
// markup:
// <div class="section {$modifiers}">
// <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b>
// report from the <a [routerLink]="['/Reports']">View Reports</a> page.</p>
// </div>
//
// .section            - Base section style with even top and bottom padding.  Use with ```.container``` classes to control width.
//  At small screen and down padding is reduced to half.  This is the base class and must be used with all modifying classes listed below.
// .section-medium-thin    - Section with half the padding of ```.section```.
// .section-bottom-medium-thin    - Can be used with or without ```section``` class so padding is only added to bottom. Bottom padding is height of ```medium-thin-section```.
// .section-top-medium-thin       - Can be used with or without ```section``` class so padding is only added to top. Top padding is height of ```medium-thin-section```.
// .section-bottom-thin    - Can be used with or without ```section``` class so padding is only added to bottom. Bottom padding is height of ```thin-section```.
// .section-top-thin       - Can be used with or without ```section``` class so padding is only added to top. Top padding is height of ```thin-section```.
// .section-clearfix   - Section with clearfix when needed
// .section-light-teal    - Section with light teal background
// .section-teal       - Section with teal background. Text color should be white against this background for contrast.
// .section-light-gray - Section with light gray background
// .section-gray       - Section with gray background
// .section-purple     - Section with purple background. Text color should be white against this background for contrast.
// .section-blue       - Section with blue background
// .section-light-pink - Section with light pink background
// .section-beige      - Section with beige background
// .section-red      - Section with red background
//
// Styleguide 4.3.2

.section {
  padding-bottom: 2*$base-spacing;
  padding-top: 2*$base-spacing;
  @include breakpoint($small-screen-down) {
    padding-bottom: $base-spacing;
    padding-top: $base-spacing;
  }
  //also doesn't need to be used with section
  &-medium-thin {
    padding-bottom: $base-spacing;
    padding-top: $base-spacing;
  }
  &-bottom-medium-thin {
    padding-bottom: $base-spacing;
  }
  &-top-medium-thin {
    padding-top: $base-spacing;
  }
  &-bottom-thin {
    padding-bottom: $base-spacing /2;
  }
  &-top-thin {
    padding-top: $base-spacing /2;
  }
  &-clearfix {
    @include clearfix;
  }
  &-light-teal {
    background: $light-teal;
  }
  &-teal {
    background: $teal;
  }
  &-gray {
    background: $background-light-gray;
  }
  &-light-gray {
    background: $white-gray;
  }
  &-purple {
    background: $purple;
  }
  &-blue {
    background: $medium-blue;
  }
  &-light-pink {
    background: lighten($light-pink, .25%);
  }
  &-beige {
    background: $beige;
  }
  &-red {
    background: $error;
  }
  &-lemon-yellow {
    background: $lemon-yellow;
  }
}

@include breakpoint($small-screen-down) {
  .section-small-screen-no-padding {
    padding: 0;
  }
}
