// These are the default Bootstrap styles that need to be added in as we aren't using Bootstrap CSS files
// All custom overrides are also here

.bootstrap-select .dropdown-menu {
 position: absolute;
 top: 100%;
 left: 0;
 z-index: 1000;
 display: none;
 float: left;
 min-width: 160px;
 //padding: 5px 0;
 margin: 2px 0 0;
 //font-size: 14px;
 text-align: left;
 list-style: none;
 background-color: #fff;
 -webkit-background-clip: padding-box;
 background-clip: padding-box;
 border: 1px solid #ccc;
 border: 1px solid rgba(0,0,0,.15);
 border-radius: 4px;
 -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
 box-shadow: 0 6px 12px rgba(0,0,0,.175);
}

.bootstrap-select .dropdown-menu>li>a {
 display: block;
 padding: .5em 1em;
 clear: both;
 font-weight: 400;
 color: $base-font-color;
 white-space: nowrap;
}

.bootstrap-select.btn-group .dropdown-menu li a:focus {
  outline: thin dotted #333333 !important;
}

.bootstrap-select .dropdown-menu>li>a:focus, .bootstrap-select .dropdown-menu>li>a:hover {
//color: #262626;
text-decoration: none;
background-color: $white-gray;
}
.btn-group, .btn-group-vertical {
 position: relative;
 display: inline-block;
 vertical-align: middle;
}
.btn {
 display: inline-block;
 padding: $small-spacing 12px;
 margin-bottom: 0;
 font-size: 14px;
 font-weight: 400;
 line-height: 1.42857143;
 text-align: center;
 white-space: nowrap;
 vertical-align: middle;
 -ms-touch-action: manipulation;
 touch-action: manipulation;
 cursor: pointer;
 -webkit-user-select: none;
 -moz-user-select: none;
 -ms-user-select: none;
 user-select: none;
 background-image: none;
 border: 1px solid transparent;
 border-radius: 4px;
}
.btn-default {
 //color: #333;
 //background-color: #fff;
 background-color: $background-light-gray;  //override style
 //border-color: #ccc;
 border-color: $base-border-color;
}

.pull-left {
 float: left!important;
}

.open>.dropdown-menu {
 display: block;
}

.bootstrap-select.open .dropdown-menu.inner {
  display: block;  // necessary because Bootstrap-accessibility is causing conflict with bootstrap-select plugin
}

.bootstrap-select.btn-group .dropdown-toggle .filter-option {
  font-size: initial;
  font-weight: $bold;
}

.bootstrap-select li {
    border-bottom: 1px solid $light-gray;
    &:last-child {
      border-bottom: 0;
    }
}

#chooseCohort {
  .bootstrap-select:not([class*="col-"]):not([class*="form-control"]):not(.input-group-btn) {
    @include span(2 of 3);
  }
}

.bootstrap-select, .selectpicker {
  flex: none;  //allows data-width to be set to auto as main element is set to flex: 1
  flex-grow: initial;
}

.btn-group.bootstrap-select {
  @include breakpoint($extra-small-screen-down) {
    width: 100% !important;
 }
}

.selectpicker:disabled + .bootstrap-select {
  opacity: .5;
}

.selectpicker:disabled + .bootstrap-select button {
  cursor: not-allowed;
}

.selectpicker:disabled + .bootstrap-select:focus {
  outline: none !important;
}

//Custom style
.bootstrap-select.disabled {
  opacity: .5;
}
