// modules/_footer.scss

/*footer {
  flex: none;
}*/

//footer logged out
.help-link:before {
  @include retina-sprite($info-icon-group);
  content:" ";
  display: inline-block;
  left: 1em;
  margin: 0 .4em 0 .8em;
  vertical-align: text-bottom;
}

//footer on logged in pages

.footer-logged-in {
  background-color: $footer-gray;
  color: $black;
}

.footer-grid {
  padding-bottom: 15px;
  padding-top: 25px;
  a {
    color: $black;
    display: inline-block;
    font-size: em(14);
    margin-left: 3em;
    margin-bottom: 10px;
    position: relative;
    @include breakpoint($medium-screen-down) {
      margin-left: 0;
    }
    &:after {
    	content: '';
    	position: absolute;
    	bottom: 0;
    	left: 0;
    	height: 0px;
    	width: 100%;
    	transition: all ease-in 0.3s;
    }
    &:hover:after {
    	height: 1px;
    	background: $black;
    }
  }

  div {
   @include layout($susy-wide-gutters);
   @include span(3);
   &:last-child {
     @include last;
   }
   @include breakpoint($small-screen-down) {
     @include span(6);
     &:nth-child(2) {
       @include last;
     }
   }
   @include breakpoint($extra-small-screen-down) {
     @include span(12);
     text-align: center;
   }
  }
}

.footer-copyright {
  border-top: 1px solid $white;
  font-size: em(14);
}
