.kaplan-profiles {
  margin-top: -5.6em;
  text-align: center;
}

.section-profile-headings {
  padding: 15px 0 33px;
}

.profile-image {
  border: 2px solid $white;
  display: block;
  margin: 0 auto;
  max-height: 131px;
  max-width: 131px;
}

.profile-name {
  color: $purple;
  font-weight: $bold;
  line-height: 1.25;
  margin: .25em 0 0 0;
}

.kaplan-consultant-profiles-section {
  .section-purple {
    height: 105px ;
  }
  .profiles-heading {
    color: $white;
    font-size: em(22);
    margin-bottom: .4em;
    text-align: center;
     @include breakpoint($medium-screen-down) {
       font-size: em(20);
     }
  }
  .bottom-grid {
    @include container;
    .bottom-grid-cell {
      float: left;
      width: 50%;
        &-left {
          border-right: 1px solid $base-border-color;
        }
      @include breakpoint($small-screen-down) {
        width: 100%;
        &-left {
          border-right: none;
          padding: 0;
        }
      }
    }
  }
  .bottom-grid-cell {
    margin-top: -105px;
    &-right {
    @include breakpoint($small-screen-down) {
      margin-top: 0;
     }
    }
  }
}

.for-help-list {
  padding-left: 2em;
  margin: 0 auto 1em;
  max-width: 20em;
}

