 .individual-profile {
   border-top: 125px solid $purple;
   font-weight: bold;
   padding-bottom: $small-spacing;
   .kaplan-profiles {
     margin-top: -6.4em;
     text-align: center;
   }
 }

 .profile-image-large {
   @extend .profile-image;
   max-height: 190px;
   max-width: 190px;
 }

 .action-links li {
   font-weight: bold;
   margin-bottom: .35em;
 }

 .contact-details {
   &-info {
     display: inline-block;
     padding-bottom: .5em;
   }
   a:first-child {
     margin-right: 2em;
   }
   .icon {
     @include breakpoint($small-phone-screen-down) {
       display: block;
     }
   }
 }

 .consultant-heading {
   font-size: em(24);
   @include breakpoint($small-phone-screen-down) {
     font-size: 1.4em;
     line-height: 1.5;
   }
 }
