// pages/_test-scheduling.scss
// Styles common to all pages

.test-summary-heading {
background-color: #FFF9EB;
margin-bottom: 5px;
  a {
  margin-left: .5em;
  }
  &-details {
    //@include breakpoint($medium-screen-up) {
      display: inline;
      margin-right: $base-spacing/2;
      @include breakpoint($small-phone-to-medium-screen) {
      display:block;
      }
      //@include breakpoint($small-phone-screen-down) {
      //  display: inline;
      //}
  }
  &-modify {
    background-color: $light-blue;
  }
  @include breakpoint($small-phone-screen-down) {
    span {
      display: none;
    }
    a {
      margin: 0;
      &.change-date-time {
        margin-left: 1.5em;
      }
    }
  }
}

.breadcrumb-number {
  width: 32px;
  height: 32px;
  background-color: #ACB5C0;
  border-radius: 50%;
  text-align: center;
  line-height: 1.85;
  font-weight: bold;
  color: white;
  font-size: 18px;
  margin: 0 auto;
  margin-bottom: .2em;
   + span {
    color: $footer-gray;
    @include breakpoint($medium-screen-down) {
      display: block; //needs to be set to set line-height
      font-size: em(14);
      line-height: 1.25;
      padding-top: .25em;
    }
    @include breakpoint($small-phone-screen-down) {
      display: none
    }
  }
  &.active {
  background-color: #5A5EA9;
  &+ span {
    color: $purple;
    font-weight: $bold;
   }
  }
  &.completed-step {
    &.success-icon {
      background-color: lighten(#ACB5C0, 15%);
      display: inherit;
      padding: 0;
      left: 0;
      &::before {
      left: 4px;
      }
    }
  }
}

.mobile-breadcrumb-name {
  color: $purple;
  font-weight: $bold;
  text-align: center;
  padding-top: 4px;
  margin-bottom: -4px;
}

.manage-test-breadcrumb  {
  margin-bottom: -.25em;
  @include breakpoint($medium-screen-down) {
    padding: 0;
  }
 .grid{
    @include span(1 of 5);
    text-align: center;
    padding: 0; //remove and change padding through Susy instead
  }
}


.review-details-table {
  tr {
    th {
      font-size: em(19);
      font-weight: $bold;
      vertical-align: top;
      @include breakpoint($medium-screen-down) {
        font-size: em(17);
      }
    }
    td:nth-child(even) {
      font-size: em(18);
      @include breakpoint($medium-screen-down) {
        font-size: em(16);
      }
    }
    td:nth-child(odd) {
      text-align: right;
    }
  }
  @include breakpoint($medium-screen-up) {
    table-layout: fixed;
    tr {
      th {
        width: 20%;
      }
      td {
        &:nth-child(2) {
          width: 65%;
        }
        &:nth-child(3) {
          width: 15%;
        }
      }
    }
  }
}

  #studentsInTestingSessionConfirmation {
    @include breakpoint($medium-screen-up) {
    display: inline-block;
   + a {
      margin-left: 1.5em;
    }
  }
    @include breakpoint($extra-small-screen-down) {
      + a {
        display: block;
        + button {
         display: block;
         float: left;
         margin: $small-spacing 0;
         text-align: left;
       }
      }

    }

}

//  Schedule date and time page

.date-time-container {
  input.date, input.time {
     display: inline-block;
     @include breakpoint($medium-screen-down) {
      @include layout(12 1/3 after);
      @include span(6 of 12);
      &:last-child, &:first-child {
        @include last;
      }
    }

    @include breakpoint($extra-small-screen-down) {
     @include span(12 of 12);
   }

    @include breakpoint($medium-screen-up) {
      margin-right: 15px;
      width: 195px;
    }
  }

  input.date.end, input.time.start {
    @include breakpoint($medium-screen-down) {
    @include last;
    }
  }
}

.date-pair-span {
  display: block;
  font-weight: $bold;
  margin-bottom: 0.75em;
  text-align: center;
  @include breakpoint($medium-screen-up) {
    display: inline-block;
    margin-right: 15px;
  }
}

.modal-wide-table  {
  @include breakpoint($extra-small-screen-down) {
    font-size: .9em;
    .text-larger-purple {
      font-size: 1em;
    }
  }
}


.modify-testing-session-left, .modify-testing-session-right {
  text-align: center;
  @include breakpoint($extra-small-screen-up) {
      text-align: left;
      .anchor-button {
        display: block;
      }
    }
}

@include breakpoint($small-screen-up) {
  .modify-testing-session-left {
    float: left;
  }
  .modify-testing-session-right {
    float: right;
  }
}

.view-testing-session-main {
  .view-testing-session-link {
    @include breakpoint($small-screen-down) {
      clear: both;
      float: left;
      margin: $small-spacing 0;
    }
  }
  .page-lg-heading {
    @include breakpoint($small-phone-screen-up) {
      float: left;
    }
  }
}
