//pages/_faculty-dashboard

.dashboard-grid-container {
  @include container;
}

.dashboard-grid {
  background: #fff;
  border-top: 10px solid $teal;
  box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.13);
  height: 22.5em;
  overflow: hidden;
  padding-bottom: 1em;
  padding-top: 1em;
  position: relative;
  margin-top: 1.5em;
  @include span(6);
   @include breakpoint($medium-screen-down) {
     @include span(12);
     height: auto;
   }
   &:nth-child(even) {
     @include last;
   }

   //@media screen-down and (min-width: 678px) and (max-width: 768px) {
  //  &-wide {
  //  width: 48.8%;
  //  margin-right: 0;
  //  clear: none;
  //  height: 24em;
   //}
    //.text-larger-teal {
    //  font-size: 1em;
  //  }
  //}

  &.dashboard-grid-inner-container, .dashboard-grid-inner-container {
    padding-left: 1.1em;
    padding-right: 1.1em;
  }

  .dashboard-grid-bottom-link {
    left: $base-spacing;
    position: absolute;
    bottom: 1em;
    @include breakpoint($medium-screen-down) {
      position: static;
    }
  }
  .indented-link {
    margin-left: 0;
    @include breakpoint($medium-screen-down) {
      margin-left: 1.1em;
    }
  }
  table tr td {
   font-size: .95em;
  }
}


.video-grid {
  margin-top: .5em;
  position: relative;
  text-align: center;
  p {
    margin-bottom: $base-spacing;
  }
  @include breakpoint($small-screen-up-to-large-screen) {
    padding-left: 2.5em;
  }
  @include breakpoint($medium-screen-up) {
    margin-top: 0;
    text-align: left;
    p {
      margin-bottom: inherit;
    }
    h2 {
      font-size: 1.95em;
    }
    &-item:first-child {
      @include span(4);
      padding-right: 1em;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
    &-item:last-child {
      @include span(8);
      @include last;
    }
  }
  @include breakpoint($large-screen-up) {
    h2 {
      font-size: 2.25em;
    }
  }
}


.breadcrumb {
  @include container;
  @include breakpoint($small-screen-down) {
    text-align:center;
  }
    &--wide {
      @include span(8);
        @include breakpoint($small-screen-down) {
          @include span(12);
        }
    }
    &-narrow {
      @include span(4);
      @include last;
        @include breakpoint($small-screen-down) {
            float: none;
            width: 100%;
        }
      > .button {
        display: inline-block;
        float: right;
          @include breakpoint($small-screen-down) {
            float: none;
            margin-top: 1.5em;
            width: 100%;
          }
         a {
           color: #fff;
         }
      }
    }
}

.bar-chart {
  @include container();
  max-width: 55%;
  &-teal {
    @include span(6);
    background: $teal;
    height: 13em;
  }
  &-purple {
    @include span(6);
    @include last;
    background: $purple;
    height: 11em;
    position: relative;
    top: 2em;
   }
  .bar, .below {
    color: $white;
    > .percent {
      text-align: center;
      font-weight: bold;
      font-size: 2em;
      display: inherit;
      padding-top: 1em;
    }
  }

  .below {
    @include span(6);
    font-weight: 800;
    color: $teal;
    margin-top: .5em;
    margin-bottom: .5em;
    text-align: center;
     &:last-child {
       @include last;
       color: $purple;
     }
  }
}

//Begin Faculty Links

.tools-link-grid {
  @include container;
  @include breakpoint($medium-screen-down) {
      @include span(12);
      border-right: 0;
      text-align: center;
  }
  > h4 {
    margin-bottom: 0.4em;
  }
}

.reporting-tools-grid  {
  @include layout(12 4 inside);
  @include span(6);
  padding-left: 0;
  @include breakpoint($medium-screen-down) {
      @include span(12);
  }
}

.faculty-link-list {
  margin: 0 3em;
  border-bottom: 1px solid $base-border-color;
  padding: .5em;
  padding-bottom: 1.2em;
    &:not(:first-child) {
     padding-top: 1.3em;
    }
    &:last-child {
      border-bottom: none;
    }
    @include breakpoint($small-screen-down) {
      @include span(12);
      //text-align: center;
      margin-top: 1em;
    }
    @include breakpoint($large-screen-down) {
      margin: 0 1em;
    }
    @include breakpoint($medium-screen-down) {
      margin: 0 1em 0 0;
      padding-left: 0;
    }
}

.teaching-tools-grid{
  @include layout(12 4 inside);
  @include span(6);
  padding-right: 0;
  border-left: 2px solid $base-border-color;
  @include breakpoint($medium-screen-down) {
      @include span(12);
      border-left: 0;
      margin-top: $small-spacing;
      text-align: center;
  }
  .faculty-link-list {
    > span {
      color: $teal;
      font-size: em(14px);
    }
    .bullet {
      @include breakpoint($medium-screen-down) {
       text-align: left;
        margin-left: 2em;
      }
       @include breakpoint($small-screen-down) {
         margin-left: 0;
     }
    }
  }
}

//End Faculty Links

.performance-snapshot {
  @include clearfix;
  //border-bottom: 1px solid $base-border-color;
  //margin-bottom: $base-spacing /2;
  //padding-bottom: $base-spacing /2;
  > .text-larger-teal {
    padding-top: 1em;
    margin-bottom: .6em;
  }
}
.performance-snapshot:nth-child(3){
    border-bottom: 1px solid #e3e3e3;
    margin-bottom: 0.75em;
    padding-bottom: 0.3em;
}
