.nav-items {
  color: $white;
  font-size: em(20);
  font-weight: $bold;
  text-align: center;
  > .nav-icon {
  display: inline-block;
  margin: .4em 2.5em;
  @include breakpoint($large-screen-down) {
    margin: .4em 1.5em;
  }
  @include breakpoint($medium-screen-down) {
    margin: .4em 1.5em;
  }
   @include breakpoint($small-phone-screen-down) {
     margin: .4em 0;
     width: 100%;
  }
  > a {
    color: $white;
  }
}

  %navigation-bar-icon {
    content: " ";
    display: inline-block;
    left: -0.5em;
    position: relative;
  }
  .schedule-icon::before {
    @extend %navigation-bar-icon;
    @include retina-sprite ($schedule-icon-group);
    top: 0.1em;
  }
  .email-icon-outline::before {
    @extend %navigation-bar-icon;
    @include retina-sprite ($email-icon-outline-group);
  }
  .info-icon-white::before{
   @extend %navigation-bar-icon;
   @include retina-sprite ($info-icon-white-group);
   top: 0.2em;
  }
}
