// utilities/_helpers.scss

// Display/Positioning Classes
//
// Styleguide 1.6

// Positioning helper classes
//
// markup:
// <div style="background-color: $light-teal; max-width: 25%;" class="{$modifiers}">{$modifiers}</div>
//
//sg-wrapper:
// <div style="max-width: 50%; border: 1px solid gray; overflow: hidden;">
//   <sg-wrapper-content/>
// </div>
//
// .left - floats element left
// .right - floats element right
// .inline, .display-inline-block - displays inline-block
// .block - displays block
// .centered - centers element by setting display to block and margin to 0 auto.
// .center  - aligns text center
// .clear - clears left and right sides of element
// .clearfix  - adds clearfix styles
// .fixed-position - fixed positions element
// .relative-position  - relative positions element (most useful for using absolute positioning on child element)
// .vertically-centered  - vertically center an element inside a relatively positioned parent
//
// Styleguide 1.6.1.

.left {
  float: left;
}

.right {
  float: right;
}

.inline {
  display: inline-block;
}

.display-inline-block {
  display: inline-block;
}

.block {
  display: block;
}

.centered {
  display: block;
  margin: 0 auto;
}

.center {
  text-align: center;
}

.clear {
  clear: both;
}

.clearfix {
  @include clearfix;
}

.fixed-position {
  position: fixed;
}

.relative-position {
  position: relative;
}

.vertically-centered {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
