//pages/_review=test-session-details

#studentsInTestingSessionTable_wrapper {
  border: 2px solid $base-border-color;
  border-radius: $base-border-radius;
  @include breakpoint($medium-screen-down) {
    margin-top: 1em;
  }
}

.name-testing-session {
  .one-half-grid {
    @include span(6 of 12 .25 after);
    &:nth-child(2), &:nth-child(4) {
      @include span(last 6 of 12 .25 after);
    }
    .bootstrap-select:not([class*="col-"]):not([class*="form-control"]):not(.input-group-btn) {
      width: 100%;
    }
    p {
      margin-top: 1.85em;
    }
  }
  @include breakpoint($medium-screen-down) {
    .one-half-grid {
      //@include span(12 of 12);
      width: 100%;
      &:nth-child(2), &:nth-child(4){
      //@include span(12 of 12);
      width: 100%;
      &:nth-child(2) p {
        margin-top: $small-spacing;
        margin-bottom: $base-spacing;
      }
    }
  }
}
}

//Custom style because negative margin of .lightbulb-icon-container-flush-text doesn't work in grid
@include breakpoint($extra-small-screen-down) {
  .name-testing-session {
    .lightbulb-icon-container .lightbulb-icon:before {
      display: block;
      float: none;
      margin: 0 auto;
      margin-bottom: .5em;
      //margin-top: -1.5em;
      margin-top: 1em; //to account for lightbulb-icon at top
    }
  }
}

.saving-testing-session-message {
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  width: 100%;
}
