//       <li><a href="https://nursing-assets-web-ktp.s3.amazonaws.com/admin/blueprints/Community_Test_A.pdf" target="_blank" class="icon pdf-download-icon link-darker">RN Community A Blueprint Jan 2016</a></li>
//       <li><a href="https://nursing-assets-web-ktp.s3.amazonaws.com/admin/blueprints/Community_Test_A_Repeat.pdf" target="_blank" class="icon pdf-download-icon link-darker">RN Community A, Repeat Blueprint Jan 2016</a></li>
//     </ul>
//   </div>
