//
// .                          - default expander
// .expander-trigger-no-line  - removes line below expander
// .expander-trigger-no-line-thin  - 
//Styleguide 3.5.1

.expander {
  @include clearfix;
  $base-font-size: 1em !default;
  $base-line-height: 1.5em !default;
  $base-spacing: 1.5em !default;
  $base-link-color: $action-color !default;
  $base-font-color: $dark-gray !default;
  $expander-toggle-size: 1em;
  margin-bottom: $small-spacing;

  &-trigger {
    @include user-select(none);
    border-bottom: 1px solid $light-gray;
    color: $base-link-color;
    cursor: pointer;
    display: block;
    font-size: $expander-toggle-size;
    margin-bottom: $expander-toggle-size;
    padding-left: $expander-toggle-size * 2;
    padding-bottom: $expander-toggle-size / 4;
    position: relative;
    text-decoration: none;
    &:before {
      @include retina-sprite($arrow-forward-orange-group);
      content: " ";
      position: absolute;
      top: 5px;
      left: 6px;
      right: 0;
      bottom: 0;
      transform: rotate(90deg);
    }
    &.collapsed:before {
      top: 5px;
      left: 10px;
      width: 12px;
      transform: rotate(0deg);
    }
    &-no-line {
      border-bottom: 0;
      &-thin {
        border-bottom: 0;
        margin-bottom: $base-spacing/4;
      }
    }
    & + .collapse {
      padding-left: $expander-toggle-size * 2;
      }
  }

  .expander-content p {
    color: $base-font-color;
    line-height: $base-line-height;
  }

}

// Large Expander
//
// markup:
// <div class="large-expander">
//   <div class="relative-position">
//     <a class="large-expander-trigger collapsed" role="button" data-toggle="collapse" href="#cohort5" aria-expanded="false" aria-controls="cohort5">
//     Extensions
//     </a>
//     <span class="vertically-centered">0 students</span>
//   </div>
//   <div class="collapse" id="cohort5">
//     <a href="roster-extend-access.html" class="right mobile-left-top-margin">Request extended access</a>
//     <p>There are no students in this cohort roster.</p>
//   </div>
// </div>
//
//Styleguide 3.5.2

.large-expander {
  @include clearfix;
  $base-font-size: 1.5em !default;
  $base-line-height: 1.5;
  $base-spacing: 1.5em !default;
  $base-link-color: $action-color !default;
  $expander-toggle-size: 1em;
  margin-bottom: $small-spacing;
  &-trigger {
    @include user-select(none);
    color: $base-link-color;
    cursor: pointer;
    display: block;
    background-color: lighten($action-color-light, 3%);
    border: 1px solid #f1d0b9;
    border-radius: $base-border-radius;
    padding: $base-spacing/3 ($expander-toggle-size *2);
    font-size: $expander-toggle-size;
    position: relative;
    text-decoration: none;
      &:before {
        @include triangle(16px, $action-color, right);;
        content: " ";
        position: absolute;
        top: 16px;
        left: 15px;
        right: 0;
        bottom: 0;
        transform: rotate(90deg);
      }
      &.collapsed:before {
        top: 16px;
        left: 15px;
        width: 12px;
        transform: rotate(0deg);
      }

      // CURRENTLY THROWS AN ERROR WHEN BUILDING THE STYLEGUIDE
      // @at-root {
      //   a#{&} {
      //     font-size: em(20);
      //     font-weight: $bold;
      //   }
      // }

      + span {
        right: $expander-toggle-size;
      }
      &-no-line {
        border-bottom: 0;
      }
  }
  .collapse {
    margin-bottom: $base-spacing;
    margin-top: $base-spacing;
  }

  .expander-content p {
    color: $base-font-color;
    line-height: $base-line-height;
  }
}

// remove once styleguide allows @at-root and #{&}
a.large-expander-trigger {
  font-size: em(20);
  font-weight: $bold;
}

