//base/_typography.scss

//body class necessary for styleguide to implement body styles within shadow dom
// See https://github.com/SC5/sc5-styleguide#html-and-body-styles and
// https://github.com/SC5/sc5-styleguide/issues/623

// Typography
//
// Styleguide 1.3

// Base body font
//
// markup:
// <p>Nostrud Truffaut, Helvetica duis brunch cronut laboris next level. Biodiesel migas occaecat, anim mlkshk culpa ullamco banjo flannel street art consequat lomo quis church-key. Deep v messenger bag cillum, McSweeney's aliqua Neutra Austin polaroid letterpress ugh raw denim officia. Sustainable eu cronut pug, tilde Portland enim chambray synth you probably haven't heard of them chia paleo. Bicycle rights nesciunt selfies commodo, polaroid slow-carb tousled whatever hashtag. Dolore migas selvage mumblecore, biodiesel meditation 90's. Polaroid ullamco 8-bit vegan.</p>
// <p>Tempor placeat, anim master cleanse narwhal put a bird on it. Retro sed Carles flexitarian. Quinoa Banksy jean shorts, tousled velit Austin cardigan sriracha Tumblr Helvetica farm-to-table plaid. Mlkshk velit cray, swag McSweeney's irure authentic Pinterest taxidermy mixtape Williamsburg drinking vinegar. Master cleanse velit bespoke Tumblr Brooklyn labore. Vinyl yr Helvetica Bushwick brunch post-ironic Vice, cliche semiotics wolf exercitation. Assumenda voluptate hashtag migas consequat shabby chic pug laboris quis wayfarers lo-fi.</p>
//
// p - Base body font is set at 100% on html and gradually decreased with viewport width.
// At breakpoint($large-screen-down) it is set at 96%.
// At @include breakpoint($small-screen-down) it is set at 94%.
//
// Styleguide 1.3.1
body, .body {
  @include font-feature-settings("kern", "liga", "pnum");
  -webkit-font-smoothing: antialiased;
  color: $base-font-color;
  font-family: $base-font-family;
  line-height: $base-line-height;
}

p {
  margin: 0 0 $base-spacing/2;
}

// Headings
//
// markup:
// <h1>Heading 1</h1>
// <h2>Heading 2</h2>
// <h3>Heading 3</h3>
// <h4>Heading 4</h4>
// <h5>Heading 5</h5>
// <h6>Heading 6</h6>
//
// These are the base headings.  Other text tags can be made the same size by giving them the respective class.
// ie using the h1 class will make an h4 tag the same size as using an h1 tag
//
// Styleguide 1.3.2
h1,
h2,
h3,
h4,
h5,
h6 {
  color: $base-heading-color;
  font-family: $heading-font-family;
  font-weight: $bold;
  line-height: $heading-line-height;
  margin: 0 0 $small-spacing;
}

h1, .h1 {
  font-size: em(36);
}

h2, .h2 {
  font-size: em(28);
}

h3, .h3 {
  font-size: em(26);
}

h4, .h4 {
  font-size: em(22);
}

h5, .h5 {
  font-size: em(20);
}

h6, .h6 {
  font-size: em(18);
}

.base-font-color {
  color: $base-font-color;
}

// Larger Fonts
//
// For when slightly larger than normal body font is needed
//
// markup:
// <p class="{$modifiers}">{$modifiers}</p>
//
// .text-larger                - Larger text
// .text-larger-teal           - Larger text, teal color
// .text-larger-purple         - Larger text, purple color
//
// Styleguide 1.3.3


@mixin text-larger($color: inherit, $font-size: em(18)) {
  color: $color;
  font-size: $font-size;
}

.text-larger {
  @include text-larger();
  &-teal {
  @include text-larger($teal);
  }
  &-purple {
  @include text-larger($base-heading-color);
  }
}

// Page Headings
//
// markup:
// <h2 class="{$modifiers}">{$modifiers}</h2>
//
// .page-sm-heading-teal       - Larger text, teal color
// .page-lg-heading           -  Even Larger text, purple color
//
// Styleguide 1.3.4

.page-sm-heading-teal {
  @include text-larger($teal, em(22));
  @include breakpoint($extra-small-screen-down) {
    text-align: center !important;
  }
}

.page-lg-heading {
  color: $base-heading-color;
  display: inline-block;
  font-size: em(28);
  font-weight: $bold;
  margin-bottom: .2em;
  @include breakpoint($small-phone-screen-down) {
    display: block;
  }
  @include breakpoint($extra-small-screen-down) {
    text-align: center;
  }
}

.section-heading-teal {
  @include text-larger($teal, em(22));
}

.section-heading-purple {
  @include text-larger($purple, em(22));
}



// Alternative margin bottom
//
// markup:
// <h2 class="{$modifiers}">{$modifiers}</h2>
// <h2 class="{$modifiers}">{$modifiers}</h2>
//
// .text-larger-margin      - Gives a margin bottom of $base-spacing
//
// Styleguide 1.3.5

.text-larger-margin {
  margin-bottom: $base-spacing;
}

.text-smaller-margin {
  margin-bottom: $small-spacing / 2;
}

.no-margin-bottom {
  margin-bottom: 0;
}

// Smaller font
//
//  markup:
// <p class="smaller">Smaller</h2>
//
// .smaller      - Slightly smaller than normal font size
//
// Styleguide 1.3.6

.smaller {
  font-size: em(14);
}

.smaller-small-text {
  font-size: em(12);
}

// Base link
//
// markup:
// <a {$modifiers}>Base link</a>
//
//  a        - base link
// :focus   - focus, hover, active style
//
// Styleguide 1.3.7

a {
  color: $action-color;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.1s linear;

  &:active,
  &:focus,
  &:hover {
    color: tint($action-color, 20%);
  }
}

// Alternative link colors
//
// markup:
// <a class="{$modifiers}">{$modifiers}</a>
//
// .link-base-color  - When you don't want a link to be given the default link color except on hover, focus.
// .link-darker  - Use for links or unstyled buttons against a background color when needed to pass contrast tests
//
// Styleguide 1.3.8

.link-base-color {
  color: $base-font-color;
}

.link-darker {
  color: $action-color-dark;
}

// End Links

.block-span {
  display: block;
}

.heading-inline {
  display: inline;
}

// Alternate colored fonts
//
// markup:
// <p class="{$modifiers}">{$modifiers}</p>
//
//
// .teal                       - alternative text color
// .text-purple                - alternative text color
// .error                      - red to grab users attention that an error has occured. .alert and .attention also share this style.
// .success                    - used for success messages
//
// Styleguide 1.3.9

.teal {
  color: $teal;
}

.text-purple {
  color: $purple;
}

.error, .alert, .attention {
  color: $error;
}

.success {
  color: $success;
}

// White colored fonts
//
// markup:
// <p class="{$modifiers}" style="background-color: $orange; padding: 3px 8px; max-width: 125px;"">{$modifiers}</p>
//
// .white-text                 - use against darker backgrounds.  Make sure color contrast is accessible
//
// Styleguide 1.3.10

.white-text {
  color: $white;
}

// Asterick text
//
// markup:
// <p class="asterick">Asterick</h2>
//
//sg-wrapper:
// <div style="border: 1px solid grey; max-width: 500px; padding: 10px">
//   <sg-wrapper-content/>
// </div>
//
// .asterick                 - Small text for disclaimer/footnote type text floated right
//
// Styleguide 1.3.11

.asterick {
  text-align: right;
  font-size: modular-scale(-1);
  margin-bottom: .2em;
  padding-right: 1.5em;
}

// Normal weight text
//
// markup:
// <h3 class="normal">Normal</h3>
//
// .normal                 - Can be added to any class or heading with font-weight bold, to make it normal weight
//
// Styleguide 1.3.12

.normal {
  font-weight: normal;
}

b, strong, .bolded {
  font-weight: $bold;
}

.initial {
  color: $base-font-color;
}

// indented text
//
// markup:
// <p class="{$modifiers}">{$modifiers}</h2>
//
// .indented                 -  adds a margin left of $base-spacing
// .indented-wide            -  adds a margin left of $base-spacing * 1.5
//
// Styleguide 1.3.13
.indented {
  margin-left: $base-spacing;
  &-wide {
    margin-left: $base-spacing*1.5;
  }
}

//Currently not using this as a style

.tag-line {
  color: $base-heading-color;
  font-style: italic;
  font-size: em(14);
}
// Center text at mobile size
//
// markup:
// <p class="mobile-center">Mobile Center</p>
//
// .mobile-center            -  Centers text at $small-screen-down
//
// Styleguide 1.3.14

.mobile-center {
  @include breakpoint($small-screen-down) {
    text-align: center;
  }
}


// Left align text or button at mobile size
//
// markup:
// <p>Text above with small margin bottom</p>
// <button class="button {$modifiers}">class="{$modifiers}"</button>
//
// .mobile-left           -  Left aligns text or button at $small-screen-down
// .mobile-left-top-margin    -  Left aligns text or button at $small-screen-down with top padding
//
// Styleguide 1.3.15

.mobile-left {
  @include breakpoint($small-screen-down) {
      text-align: left;
      width: 100%;
    &-top-margin {
      @extend .mobile-left;
      margin-top: 1em;
    }
  }
}

.dashboard-grid > h3 {
  font-size: em(20);
  color: $teal;
   .smaller {
     font-size: modular-scale(-1);
     font-weight: normal;
   }
}

// Dynamically bold first 5 words
//
// markup:
// <p class="js-bolded">Bolder text will appear here, and not here with JavaScript magic.</p>
//
// Styleguide 1.3.16

.text-link-color {
  color: $action-color;
}

.text-extra-mobile-spacing:not(:last-child) {
    @include breakpoint($extra-small-screen-down) {
      margin-bottom: 1em;
    }
}

.white-link {
  &:active,
  &:focus,
  &:hover {
    color: $white;
  }
}

section {
  .text-section {
    margin-bottom: $base-spacing;
  }
}
