// base/_grid-settings-susy.scss

// Grids
//
//  We use Susy for a flexibile grid solution -- http://susy.oddbird.net/
//  The default 12 column grid is set here with the $susy variable, but custom grids can be made as one offs or as new, custom grids with individual layout maps to access as needed.
//  Column number, width, gutter width, gutter position and more are all customizable. See the docs for more info: http://susydocs.oddbird.net/
//
// Styleguide 1.5

$susy: (
  flow: ltr,
  math: fluid,
  output: float,
  gutter-position: after,
  container: 74em,
  container-position: center,
  columns: 12,
  gutters: .25,
  column-width: false,
  global-box-sizing: border-box,
  last-flow: to,
  debug: (
    image: hide,
    color: rgba(#66f, .25),
    output: background,
    toggle: top right,
  ),
  use-custom: (
    background-image: true,
    background-options: false,
    box-sizing: true,
    clearfix: false,
    rem: true,
  )
);

//Use with @include layout($susy-wide-gutters)
//before the @include span(number) for gutter twice as wide as normal
$susy-wide-gutters: (gutters: .5);
