.dropdown {
  $dropdown-color: $white;
  //$dropdown-description-color: lighten($dropdown-color, 40%);
  $dropdown-border: 2px solid $base-border-color;
  $dropdown-background: darken($dark-gray, 4% );
  $dropdown-background-hover: darken($dropdown-background, 7%);
  $dropdown-background-lighter: lighten($dropdown-background, 5%);
  $dropdown-height: 30px;
  $dropdown-padding: 1em;
  $dropdown-distance-from-menu: 2.5em;
  $dropdown-arrow-top-distance: 0;
  $full-width-mode: $small-screen-down;
  float: right;
  font-size: em(18);
  font-weight: normal;
  line-height: $base-line-height;
  @include breakpoint($full-width-mode) {
    float: none;
    text-align: center;
  }

  .dropdown-container {
    position: relative;
    text-align: center;
    @include breakpoint($full-width-mode) {
      border-top: 1px solid $medium-gray;
      display: inline-block;
      width: 100%;
    }
  }

  .dropdown-button {
    color: #fff;
    cursor: pointer;
    display: block; //stops text from shifting on menu open/close
    float: right;
    line-height: $dropdown-height;
    position: relative;
    @include breakpoint($full-width-mode) {
      float: none;
      font-size: 1.1em;
      line-height: $dropdown-height * 1.5;
      padding: 0;
    }
    &:after {
      @include retina-sprite($arrow-drop-down-group);
      content: " ";
      display: inline-block;
      margin-left: .75em;
    }
  }

  .dropdown-menu {
    @include transition (all 0.2s ease-in-out);
    background: $dropdown-background;
    border: $dropdown-border;
    box-shadow: 0 2px 2px transparentize(black, 0.8);
    color: $dropdown-color;
    cursor: pointer;
    display: none;
    margin: 0;
    overflow: show;
    position: absolute;
    right: 0;
    top: $dropdown-distance-from-menu;
    z-index: 99999;
    @include breakpoint($full-width-mode) {
      border: 0;
      padding: 0;
      position: static;
      width: 100%;
    }
    @media (max-height: 476px) and (orientation: landscape) {  //make menu scroll instead of being cut off in phone landscape view
      height: 200px;
      overflow-y: auto;
      position: absolute;
    }
  }

  .dropdown-menu li {
    //border-top: 2px solid $base-border-color;
    color: $dropdown-color;
    list-style: none;
    text-align: left;
    white-space: nowrap;
    &:nth-child(even) {
     background-color: $dropdown-background-lighter;
    }
      //@include breakpoint($full-width-mode) {
        //border-top: 0;

      //}
     a {
       color: $dropdown-color;
       display: block;
       padding: 0.5em 2em .5em 3em;
       @include breakpoint($full-width-mode) {
          padding: 1em 4em 1em 5em;
       }
       @include breakpoint($small-phone-screen-down) {
          padding: .825em 4em;
       }
       @media (max-height: 476px) and (orientation: landscape)  {  //decrease padding in phone landscape view
         padding-top: $small-spacing;
         padding-bottom: $small-spacing;
        }
       &:hover, &:focus {
        background: $dropdown-background-hover;
       }
      }
  }

  .show-menu {
    display: block;
  }
}

.open > .dropdown-menu {
  display:block;
}

// icons

.menu-icon:before {
  background-repeat: no-repeat;
  content:" ";
  display: block;
  left: 1em;
  margin-top: 2px;
  position: absolute;
  @include breakpoint($small-screen-down) {
    left: 2em;
  }
  @include breakpoint($small-phone-screen-down) {
    left: $base-spacing;
  }
}

.home-icon:before {
  @include retina-sprite($home-icon-group);
}

.reporting-icon:before {
  @include retina-sprite($reporting-icon-group);
}

.student-icon:before {
  @include retina-sprite($student-icon-group);
}

.group-icon:before {
  @include retina-sprite($group-icon-group);
  margin-top: 4px;
}

.test-icon:before {
  @include retina-sprite($test-icon-group);
  margin-top: 4px;
}

.manage-icon:before {
  @include retina-sprite($manage-icon-group);
  margin-top: 3px;
}

.sign-out-icon:before {
  @include retina-sprite($sign-out-icon-group);
}
