//Login Pages

/*header {
  flex: none;
}*/

.login-header {
  @include clearfix;
  background: $white;
  padding: 2em 1em .1em;
  @media (max-height: 800px), (max-width: $large-screen-down) {
    padding: $base-spacing .5em 0;
  }
  @include breakpoint ($small-screen-down) {
    padding: .5em .5em 0;
  }
   > .center {
    @media (max-height: 800px) {
      margin-bottom: $small-spacing/2;
    }
    @include breakpoint ($small-screen-down) {
     font-size: 1.55em;
      }
  }
}

.branding-logo {
  height: 47px;
  margin-bottom: $small-spacing;
  width: 265px;
  @media (max-width: $medium-screen-down), (max-height:800px) {
    height: 39px;
    margin-bottom: $small-spacing/2;
    width: 220px;
  }
  @include breakpoint ($small-screen-down) {
    height: 34px;
    margin-bottom: 0;
    width: 190px;
  }
}

// Main Portal Pages (Gray background/white logo)

.inner-banner {
  background: $dark-gray;
  left: 0;
  padding: 1.35em 0;
  position: fixed;
  right: 0;
  width: 100%;
  top: 0;
  z-index: 2000;
  @include breakpoint($small-screen-down) {
    padding: 0;
  }
}

//Logged out header without navigation
.inner-banner-logged-out {
  @include breakpoint($medium-screen-down) {
    padding: $small-spacing 0;
  }
}

.inner-banner h1 {
  line-height: 0; //gets rid of extra space around logo
  margin-bottom: 0;
}
.branding-logo-white {
  display: block; //gets rid of padding added to outer div
  height: 30px;
  margin: 0 auto; //centers image since display is set to block
  width: 169px;
}

.branding-center {
  @include span(4);
  @include push(4);
    @include breakpoint($small-screen-down) {
      width: 100%;
      margin: 0 auto;
      padding: .5em 0;
      }
}

.inner-banner > .container {
  @include breakpoint($small-screen-down) {
    padding: 0
  }
}

//Include logo for print here hidden and show in print stylesheet
main::before {
 content: url(./assets/images/logo-purple_2x.png);
 display: none;
 width: 532px;
 height: 39px;
 margin: 0 auto;
 transform: scale(.4);
}
