// print/_print.scss

@media print {

  // helper class
  .hidden-for-print {
    display: none !important;
  }

  span.display-for-print {
    display: inline !important;
  }

  @page {
  size: auto;
  margin: 15mm;
  }

  // necessary for tables that span more than one page to print more than first page in Firefox
  body {
    display: block;
  }

  h2, h3, h4 {
   page-break-after: avoid;
  }
  // necessary for tables that span more than one page to print more than first page in Firefox
  .dataTables_wrapper {
    width: auto;
    position: relative;
    display: block;
    float: none;
  }
  //hide header and footer
  header, footer, .print-icon, .faculty-user-guide-menu, .back-to-top-arrow, .video, a[href="#focusedReview"], a[href="#integratedTesting"] {
    display: none !important;
  }

  //included in site-header.scss file with display: none, else it doesn't show up in Chrome at least on first print
  main::before {
    display: block;
  }

  .app-main {
    padding-top: 10px;
  }

  .view-testing-session-main {
    font-size: 12px;
    .page-sm-heading-teal {
      display: none;
    }
    a {
      display: none;
    }
  }

  .modify-testing-session {
    &-left, &-right {
      a, button {
        display: none !important;
      }
    }
  }

  .section-light-gray {
    background: none;
  }

  .faculty-user-guide-header {
    background: none;
    position: relative;
    top: 16px;
    h2, span {
    color: $teal;
    }
  }
  .faculty-user-guide-content {
    font-size: 90%;
    width: 100%;
    .collapse {
      display: block;
    }
  }

}
