//pages/_intersistial-choose-program.scss

.cover-header {
 position: relative;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 background-color: rgba(200,200,200, .7);
 z-index: -1;
}

.choose-program-module {
  @include clearfix;
  max-width: 555px;
  margin: 0 auto;
  @include breakpoint($small-phone-screen-down) {
    width: 367.969px;
  }
  &-grid {
  background-color: $light-teal;
  border-radius: $base-border-radius;
  float: left;
  height:176px;
  width: 226px;
  padding: 2em;
  margin: 2em 0;
  @include breakpoint($small-phone-screen-down) {
    width: 145px;
    margin-left: .5em;
  }
    &:nth-child(2) {
      float: right;
      @include breakpoint($small-phone-screen-down) {
       margin-right: .5em;
      }
    }
  }
}
