//  @site-main
//--------------------------------------------------------
//  modules/_site-main.scss
//--------------------------------------------------------

//  The site's main content on all pages except index.html login page

//------------------------------------------------------

//  The site's main logged in content pages with with nav in header

//------------------------------------------------------
//based on height of fixed nav header.  If this is  not enough padding will have
// add make section-top class to add extra padding too, so background is full bleed
.app-main {
  //flex: 1 0 auto; /* 2 */
  width: 100%;
  padding-top: em(73);
   @include breakpoint($small-screen-down) {
     padding-top: 90px;
     &-no-nav {
       padding-top: 45px;
     }
   }
   @include breakpoint($extra-small-screen-up) {
    //subtract footer height from main height for sticky footer
    min-height: calc(100vh - 214.5px);
  }
   @include breakpoint($small-screen-up) {
    //subtract footer height from main height for sticky footer
    min-height: calc(100vh - 148px);
  }
  //  &::after {
  //    content: '\00a0';
  //    display: block;
  //    height: 0px;
  //    visibility: hidden;
  //  }
}


//  The site's pages before being logged in that have header without nav

.app-logged-out {
  padding-top: 3.5em;
  //padding-top: em(75px);
   @include breakpoint($medium-screen-down) {
     //padding-top: em(55px);
     padding-top: 2.5em;
   }
}
