// modules/_button-with-success.scss

// Button with successful validation
//
// markup:
// <button class="button button-with-validation">Submit</button>
// <p class="success success-icon">You have successfully set your new password.</p>
//
// Styleguide 3.3
.button-with-validation {
  margin-right: $base-spacing;
}

.success-icon {
  display: inline-block;
  margin-bottom: 0;
  padding: .75em .5em .5em 43px;
  position: relative;
  &::before {
    @include retina-sprite($checkmark-validation-group);
    content: " ";
    left: 0;
    position: absolute;
  }
  + a, .success-container + a {
  clear: both;
  display: block;
  padding-top: 2em;
  }
}
