//base/_forms.scss

// Forms
//
// Styleguide 2.2

// Fieldset
//
// markup:
// <fieldset>Fieldset</fieldset>
//
// fieldset
//
// Styleguide 2.2.1

fieldset {
  background-color: lighten($base-border-color, 10%);
  border: $base-border;
  margin: 0 0 $small-spacing;
  padding: $base-spacing;
}


// Inputs, Labels, and Selects
//
// markup:
// <label for="sample">Label</label>
// <input type="text" id="sample">
// <select>
//   <option>Option 1</option>
//   <option>Option 2</option>
//   <option>Option 3</option>
//    <option>Option 4</option>
// </select>
//
// Default label and ```input[type="text"]``` and select.  These are are all set to ```display: inline-block``` by default.  Use class ```.form-control-block``` to
// have them display block instead.
//
// Styleguide 2.2.2


input,
label,
select {
  color: $dark-gray;
  display: inline-block;
  font-family: $base-font-family;
  font-size: $base-font-size;
}

.form-control-block {
  display: block !important;
}

// Label with class "required"
//
// markup:
// <label class="required" for="sample">Label for required input</label>
// <input type="text" id="sample">
//
// Adds an asterick to denote a required field
//
// Styleguide 2.2.3
label {
  margin-bottom: $small-spacing / 3;

  &.required::after {
    content: "*";
  }

  abbr {
    display: none;
  }
}

// A drop-down list that allows multiple selections
//
// markup:
// <select multiple>
//   <option>Option 1</option>
//   <option>Option 2</option>
// </select>
//
// Styleguide 2.2.4
#{$all-text-inputs},
select[multiple=multiple],
textarea {
  background-color: $base-background-color;
  border: $base-border;
  border-radius: $base-border-radius;
  box-shadow: $form-box-shadow;
  box-sizing: border-box;
  font-family: $base-font-family;
  font-size: $base-font-size;
  margin-bottom: $base-spacing / 2;
  padding: $base-spacing/2;
  transition: border-color;
  width: 100%;

  &:hover {
    border-color: darken($base-border-color, 10%);
  }

  &:focus {
    border-color: $purple !important; //not sure why this needs important. Otherwise is not applied to search inputs
    box-shadow: $form-box-shadow-focus;
    outline: none;
  }
}

// Text Area
//
// markup:
// <textarea></textarea>
//
// Styleguide 2.2.5

textarea {
  resize: vertical;
}

// Checkboxes, Radios, File, Submit
//
// Checkboxes are for selecting one or several options in a list,
// while radios are for selecting one option from many.
// Checkboxes and Radios will display ```inline``` unless they are wrapped in a ```<label>``` in which case
// they will display ```block```.  To add a margin-right for appropriate spacing for inline checkboxes and radios add class ```.checkbox-radio-inline```.
// Add class ```.form-group``` to groups of checkboxes or radios to give them appropriate spacing with margin-bottom.
//
// markup:
// <form>
//  <div class="form-group">
//    <label class="checkbox-radio-inline"><input type="checkbox" name="selection" value="optionOne">Checkbox One</label>
//    <label class="checkbox-radio-inline"><input type="checkbox" name="selection" value="optionTwo">Checkbox Two</label>
//    <label class="checkbox-radio-inline"><input type="checkbox" name="selection" value="optionThree">Checkbox Three</label>
// </div>
// <div class="form-group">
//  <label class="form-control-block"><input type="radio" name="selection1" value="optionOne">Radio One</label>
//  <label class="form-control-block"><input type="radio" name="selection1" value="optionTwo">Radio Two</label>
//  <label class="form-control-block"><input type="radio" name="selection1" value="optionTwo">Radio Three</label>
// </div>
//  <input type="file">
//  <input type="submit" value="Submit">
// </form>
//
// Styleguide 2.2.6

input[type="checkbox"],
input[type="radio"] {
  display: inline;
  margin-right: $small-spacing/2;
  position: relative;
  top: -1px;
  &.checkbox-block {
    display: block;
  }
}

.checkbox-radio-inline {
  margin-right: 1em;
}

.form-group-block {
  input[type="checkbox"],
  input[type="radio"] {
    display: block;
    + label {
      margin-bottom: $small-spacing;
    }
  }
}


