input[type="file"] {
  padding-bottom: $small-spacing;
  width: 100%;
}

input[type="submit"].button {
  color: $white;
}

select {
  margin-bottom: $base-spacing;
  max-width: 100%;
  width: auto;
}

.indented-inputs {
  padding-left: 2em;
}

// Teal label
//
// markup:
// <label class="teal-label" for="sample">Teal Label</label>
// <input type="text" id="sample">
//
//
// Styleguide 2.2.7
.teal-label {
  color: $teal;
  > label {
    @extend .teal-label;
  }
}

// Teal label
//
// markup:
// <label class="label-bold" for="sample">label-bold</label>
// <input type="text" id="sample">
// <label class="label-alert" for="sample">label-alert</label>
// <input type="text" id="sample">
//
//
// Styleguide 2.2.8

.label {
  &-bold {
    font-weight: 600;
  }
  &-alert, &-error {
    color: $alert;
    display: block;
    font-size: em(18);
  }
  &-larger {
    font-size: em(18);
    margin-bottom: $small-spacing/2;
  }
}

form .error {
  margin-top: -.3em;
}

.form {
 &-attention {
  margin-bottom: $base-spacing;
  > label{
    color: $alert;
    display: block;
    font-size: em(18);
    margin-bottom: $small-spacing;
    }
  }
 &-medium-width {
   max-width: em(650);
 }

 &-narrow {
   max-width: em(550);
 }
}

.form-group {
  display: inline-block;
  margin-bottom: 1.5em;
  vertical-align: middle;
  width: 100%;
  > input {
    display: inline-block;
    margin-bottom: 0;
    width: 74%;
    @include breakpoint($small-screen-down) {
      width: 100%;
    }
  }

  > .label-inline {
      display: inline-block;
      margin-bottom: 0;
      vertical-align: middle;
      width: 25%;
      @include breakpoint($small-screen-up) {
        padding-right: 1em;
        text-align: right;
      }
      @include breakpoint($small-screen-down) {
      width: 100%;
    }
  }

  > .label-inline-align-left {
      margin-bottom: 0;
      width: 20%;
      @include breakpoint($small-screen-up) {
        padding-right: 1em;
      }
      @include breakpoint($small-screen-down) {
      width: 100%;
    }
    + input {
      width: 79%;
      @include breakpoint($small-screen-down) {
      width: 100%;
      }
    }
  }

  > .input-aligned {
     margin-left: 20.5%;
     //margin-top: 1.5em;
     @include breakpoint($small-screen-down) {
     margin-left: 0;
     //float: right;
    }
     @include breakpoint($extra-small-screen-down) {
     margin-left: 0;
     width: 100%;
    }
    > .button {
      margin-top: 1.5em;
    }

  }

  &-narrow {
    padding-right: 20%;
      @include breakpoint($small-screen-down) {
        display: block;
        float: none;
        margin: 0 auto;
        padding-right: 0;
        width: 76%;
     }
     @include breakpoint($extra-small-screen-down) {
       width: 100%;
     }
  }
   .error {
  //  @extend .button-input-aligned;
      margin-top: .5em;
  }
}

form .datepicker-after {
  width: 85%;
}

.input-label-group-thirds {
  @include breakpoint($medium-screen-up) {
    @include span(4);
    &:nth-child(3) {
      @include last;
    }
  }
}

.form-width {
 max-width: em(460);
}
