//modules/_icon-modules.scss

// Icon Modules
//
// These containing classes are used when icons are used with longer strings of text so text will be left justified and not wrap around icon.
// Icon centers at 414px
// Cannot be used with grid because of negative margins
//
// Styleguide 3.12

// Lightbulb icon module
//
// markup:
// <div class="lightbulb-icon-container-flush-text">
//   <p class="icon lightbulb-icon">
//     Your new password must be different than your last five passwords, at least 8 characters long, and contain at least one number and at least one special character.
//     @, !, #, $, % are examples of special characters. Choose something easy for you to remember but difficult for others to figure out.
//   </p>
// </div>
//
// Styleguide 3.12.1



.lightbulb-icon-container-flush-text {
  margin-left: 47px;
  @include breakpoint($extra-small-screen-down) {
    margin-left: 0;
  }
  .lightbulb-icon::before {
    margin-left: -47px;
    @include breakpoint($extra-small-screen-down) {
    display: block;
    float: none;
    margin: 0 auto;
    margin-bottom: .5em;
    margin-top: 1em;
    }
  }
}

// Smaller icon modules with background images
//
// markup:
// <div class="icon-container-flush-text">
//   <p class="icon {$modifiers}">
//     Sample text would go here that would be long enough to break into two lines at smaller breakpoints at least.
//   </p>
// </div>
//
// .info-icon-yellow  - info icon
// .red-triangle-alert-small-icon - small red alert triangle
// .no-blueprint-icon - no blueprint icon
//
// Styleguide 3.12.2

%flush-left-text {
  margin-left: -38px;
  @include breakpoint($extra-small-screen-down) {
  display: block;
  float: none;
  margin: 0 auto;
  margin-bottom: .5em;
  margin-top: 1em;
  }
}

.icon-container-flush-text {
  margin-left: 38px;
  @include breakpoint($extra-small-screen-down) {
    margin-left: 0;
  }
  .info-icon-yellow::before {
    @extend %flush-left-text;
  }
  // no ::before because this is for use with an inline image
  .self-pay-alert-icon {
    @extend %flush-left-text;
  }

  .red-triangle-alert-small-icon::before {
    @extend %flush-left-text;
  }

  .no-blueprint-icon::before {
    @extend %flush-left-text;
  }
}

// Smaller icon module with inline image
//
// markup:
// <div class="icon-container-flush-text">
// <img src="assets/images/self-pay-alert-icon.svg" class="self-pay-alert-icon" alt="payment alert icon" height="18" width="18"/>
// <p>These students have been deactivated due to non-payment. They will not be able to access their test during the specified testing
//   window unless their balance is paid in full at least two business days prior to test day.
// </p>
// </div>
//
// Styleguide 3.12.3


// Extra small icon container with inline image
//
//  Extra small icon size and smaller spacing between icon and text to match inline icon styles
//
// markup:
// <div class="small-icon-container-flush-text">
//   <img src="assets/images/identity-icon.png" class="duplicate-student-icon" alt="duplicate student icon" height="18" width="18"/>
//   <p>
//     Student has the same first and last name as another student in this cohort roster. Click or tap on the icon to view the students email address.
//   </p>
// </div>
//
// Styleguide 3.12.4

.small-icon-container-flush-text {
  margin-left: 27px;
  .duplicate-student-icon {
    float: left;
    margin-left: -27px;
    margin-top: 3px;
  }
}
