// Base Container
//
// markup:
//  <div class="container">
//  <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b> report from the <a [routerLink]="['/Reports']">View Reports</a> page.
//  </div>
//
// Styleguide 4.2.1

//Set the default container max-width
$max-width: 74em;

.container {
  @include container($max-width);
  @extend %container-padding;
  &-mobile-wider {
    @include breakpoint($small-screen-down) {
      padding: 0 0.25*$base-spacing;
    }
  }
}

// Medium width container
//
// markup:
//  <div class="container-medium-width">
// <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b> report from the <a [routerLink]="['/Reports']">View Reports</a> page.
//  </div>
//
// Styleguide 4.2.2

.container-medium-width {
 @include container(970px);
 @extend %container-padding;
}

.container-medium-narrow-width {
 @include container(850px);
 @extend %container-padding;
}

// Narrow width container
//
// markup:
//  <div class="container-narrow">
// <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b> report from the <a [routerLink]="['/Reports']">View Reports</a> page.
//  </div>
//
// Styleguide 4.2.3

.container-narrow {
 @include container(750px);
 @extend %container-padding;
}

// Full width container
//
// markup:
//  <div class="container-full-width">
// <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b> report from the <a [routerLink]="['/Reports']">View Reports</a> page.
// </div>
//
// Styleguide 4.2.4

.container-full-width {
 @include container(100%);
 @extend %container-padding;
}

// Base container without padding
//
// markup:
//  <div class="container-no-pad">
// <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b> report from the <a [routerLink]="['/Reports']">View Reports</a> page.
//  </div>
//
// Styleguide 4.2.5

.container-no-pad {
  @include container();
}

// Medium width container without padding
//
// markup:
//  <div class="container-medium-width-no-pad">
// <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b> report from the <a [routerLink]="['/Reports']">View Reports</a> page.
// </div>
//
// Styleguide 4.2.6
.container-medium-width-no-pad {
  @include container(970px);
}

// Grid container
//
//  Applies clearfix
//
// markup:
//  <div class="grid-container">
// <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b> report from the <a [routerLink]="['/Reports']">View Reports</a> page.
//  </div>
//
// Styleguide 4.2.7
.grid-container {
 @include clearfix;
}

// Layout
//
// Styleguide 4.0

// Callout container
//
// A container for important messages.  It should always use ```.text-larger``` class for consistency. Also, if the last element is a paragraph,
// ```.no-margin-bottom``` class should be added to it so content is evenly spaced vertically within the container.
//
// markup:
//  <div class="container-medium-width thin-section">
//    <div class="section section-beige container-dull-yellow-top text-larger">
//        <p><strong>Please note:</strong> All upcoming tests scheduled through our new process will appear on this page.
//            However, there may still be upcoming tests for your school that have been scheduled through our old process.
//            <b>Information on tests scheduled through our
//    old process will not appear here.</b>
//        </p>
//        <p>You can view upcoming tests that have been scheduled through our old process by accessing the <b>Tests Scheduled by Date</b>                    report from the <a [routerLink]="['/Reports']">View Reports</a> page.
//        </p>
//        <p class="no-margin-bottom">
//            If you have any questions, please contact your Account Manager. You can find your Account Managers contact details on the
//            <a href="home.html">Home page</a>.
//        </p>
//    </div>
// </div>
//
// Styleguide 4.1

.container-dull-yellow-top {
 @extend .container;
 @extend .section-medium-thin;
 border-top: 4px solid $dull-yellow;
}

.container-teal-top {
 @extend .container;
 @extend .section-medium-thin;
 border-top: 4px solid $teal;
}

.container-purple-top {
  @extend .container;
  @extend .thin-section;
  @include breakpoint($medium-screen-down) {
    padding: 0 $base-spacing;
  }
  border-top: 4px solid transparentize($purple, .4);
}

.container-bordered {
  border: $base-border;
  border-radius: $base-border-radius;

}
