// pages/_test-scheduling-add-students.scss

//outer container
.add-students-outer-container {
  @include breakpoint($large-to-small-screen) {
    padding-left:  $base-spacing * 2;
    padding-right:  $base-spacing * 2;
  }
}
//main container
.testing-add-students-container {
  @include container;
  box-shadow: 0 0 0 2px #e3e3e3; //instead of border because box-shadow does not affect absolute positioning of elements within.
  border-radius: 4px;
  position: relative;
  @include breakpoint($large-screen-up) {
  display: table;
  table-layout: fixed;
  width: 1135px;
  }

  // custom select dropdown
  .tab-content > .tab-pane {
   padding: 0;
  }
  .grid {
    .top-section {
      padding: $small-spacing;
    }
  }
  .left-grid {
    background: $white;
    //width: percentage(2/3); //This is giving slight subpixel rounding error
    width: 757px;
    border-right: 2px solid $base-border-color;
    min-height: 400px;
    @include breakpoint($large-screen-up) {
    border-radius: 4px 0 0 4px;
    display: table-cell;
    float: none;
    vertical-align: top;
    }
    @include breakpoint($large-screen-down) {
      border-right: 0;
      min-height: 100px;
      width: 100%;
    }
  }
  .right-grid {
    //float: left; //floating left here because makes subpixel rounding error less noticable (white ~1px gap )
    //This doesn't add up too 100 with 66% for left grid, but getting small ~ pixel gap otherwise (subpixel rounding error?)
    //width: percentage(1/3);    //width: percentage(1/3); //This is giving subpixed rounding error
    width: 378px;
    @include breakpoint($large-screen-up) {
    display: table-cell;
    float: none;
    vertical-align: top;
    }
    @include breakpoint($large-screen-down) {
      width: 100%;
    }
    .top-section {
      background-color: $light-purple;
      padding: 1em;
      &.active {
        background-color: $purple;
        border-top-right-radius: $base-border-radius;
        @include breakpoint($large-screen-down) {
          border-top-right-radius: 0;
        }
      }
      &.modify.active {
        background-color: $error;
      }
    }

    .added-student-list {
      //@include breakpoint($large-screen-up) {
        //height: 100vh;
      //  height: 606px; //so background goes full height
    //  }
      background-color: $light-purple;
      color: $purple;
      font-weight: $bold;
      // margin-bottom: 49px; // since search is absolutely positioned, give margin bottom same height (-3px for some reason) as that containting
      //div so list items don't get hidden underneath it.
      ul {
        li {
          padding: .5em;
          padding-left: 1em;
          &:nth-child(odd) {
            background-color: $lightest-purple;
          }
        }
      }
    }
  }
}
// ends main container .testing-add-students-container

.small-tag-text {
  color: darken($teal, 5%);
  font-size: 12px;
  font-weight: normal;
  margin-left: .6em;
}

.students-in-testing-session-list-item {
  float: left;
  margin-right: 1%;
  width: 71%;
  span {
    line-height: 1.8;
  }
}

.testing-remove-students-button {
  float: right;
  display: inline-block;
    @include breakpoint($large-to-small-phone-screen) {
      margin-right: $base-spacing *2 ;
    }
}

.search-added-students {
  border-top: 2px solid $base-border-color;
  min-height: 53px;
  background-color: lighten($light-gray, 7%);
  position: absolute;
  bottom: 0;
  width: percentage(1/3);
  @include breakpoint($large-screen-down) {
    position: relative;
    width: 100%;
  }
}

.one-half-width {
  width: 50%;
}

// bottom section after main container
.attention-note {
  max-width: em(750px);
}

#selectedStudentsContainer {
  .lightbulb-icon {
    margin-bottom: 2em;
    &:before {
      margin-bottom: 10px;
    }
  }
}

#individualStudents {
  .add-students-table-search {
    @include breakpoint($extra-small-screen-down) {
      display: none !important;
    }
  }
}

.add-students-table-search {
  background-color: lighten($light-gray, 7%);
  border-top: 2px solid $base-border-color !important;
  height: 53px;
  @include breakpoint($large-screen-up) {
  padding: 0 2em;
  }
 .dataTables_filter {
   @include breakpoint($small-screen-up) {
   float: left !important;
   margin-left: 10px;
   margin-right: 25px;
   text-align: left !important;
   }
  }
 .form-group {
   width: auto !important;
  }
  label {
    margin-bottom: 0;
  }
  input.small-search-box {
    width: 255px;
    font-weight: inherit;
  }
}

.dataTables_wrapper.no-footer .dataTables_scrollBody {
  border-bottom: 0;
}

//adjust bottom section spacing for individual students as there is no search input
#individualStudents {
  .dataTables_scrollBody {
    border-bottom: 0;
  }
  .add-students-table-search {
    display: table;
    width: 100%;
    .form-group {
      display: table-cell;
      text-align: center;
      label.smaller {
        margin-left: 2em;
        margin-right: 2em;
      }
    }
  }
}

.top-section .button-light {
  @include breakpoint($extra-small-screen-down) {
    font-size: inherit;
    margin-top: $base-spacing;
    margin-bottom: $small-spacing;
    width: 100% !important;
  }
}

// Remove all students button
.search-added-students button {
  position: absolute;
  right: $small-spacing;
  top: 50%;
  transform: translate(0, -50%);

  @include breakpoint($large-to-small-phone-screen) {
    margin-right: $base-spacing *2 ;
  }
  @include breakpoint($small-phone-screen-down) {
  font-size: inherit;
  width: 97% !important;
  left: 50%;
  transform: translate(-50%, -50%);
  }
}

// Search input selected students
.filter-students-container {
  left: 10px;
  position: relative;
  width: 52%;
   @include breakpoint($large-screen-down) {
    width: 255px; //to match width of other search input
  }
  @include breakpoint($medium-screen-down) {
    width: 255px; //to match width of other search input
  }
}

// Filter students label
.add-students-table-search {
  label.smaller {
   margin-right: 1.5em;
   line-height: 3.5;
  }
}

//Datatables


.dataTables_wrapper .dataTables_length, .dataTables_wrapper .dataTables_filter {
  @media screen and (max-width: 768px) {
    float: none;
    text-align: center;
  }
}

//override default datatables css //why doesn't this work!!!
@media screen and (max-width: 640px) {
    .dataTables_wrapper .dataTables_filter {
     margin-top: 0 !important;
   }
 }

@include breakpoint($large-screen-down) {
  .add-students-outer-container .dataTables_scrollBody {
    height: auto !important;
  }
}

.test-scheduling-selected-students-container {
    height: 606px;
    overflow-y: auto;
    @include breakpoint($large-screen-down) {
      height: auto;
    }
}

//Center aligns "Retesting" column
 .testing-add-students-container .dataTable tr td:nth-child(4),
 .testing-add-students-container .dataTable tr th:nth-child(4) {
   text-align: center;
 }

 .dataTables_filter label {
   position: relative;
 }


// End Datatables styles

// Start Modal styles
.please-note-section {
  padding: 1em 2em;
  .default-ol li {
    margin-bottom: $small-spacing;
  }
}

//End Modal Styles
