// default tabCollapse

// Should background-color be on li or on a?.  If one tab becomes split on two lines and not another if background-color is on <a> ul will need display flex so that color is full height.
// Bootstrap puts background-color on <a>
// todo put border on each li instead of ul because of rendering issues
.tabs {
@include clearfix;
// check cross-broswer but if base-border-radius is used there appears to be a 1px gap in chrome.
// border-radius: $base-border-radius;
// border-radius: 5px;
line-height: 1.5;
padding: 0;
position: relative;
  li {
    list-style: none;
    display: inline;
    float: left;
    &:first-child a {
      // border-radius: $base-border-radius 0 0 $base-border-radius;
      &:focus {
       outline-offset: -2px;
      }
    }
    &:last-child a {
      // border-radius: 0 $base-border-radius $base-border-radius 0 ;
    }
    &.active {
      a {
        font-weight: $bold;
      }
    }
  }
  a {
    display: inline-block;
    font-size: 1.05em;
    padding: .6em 1em;
    text-decoration: none;
    &:focus  {
      margin-bottom: 0 !important;
      outline-offset: -2px;
      outline: none;
    }
  }
}

@mixin tab-settings ($borders, $active-tab-color, $inactive-text-color) {
  li {
    a {
      border-bottom: $base-border-darker;
      border-top: $base-border-darker;
      border-right: $base-border-darker;
      color: $inactive-text-color;
      text-align: center;
      &:focus  {
        outline: 1px dotted #C9C9C9;
      }
      &:hover  {
         color: tint($inactive-text-color, 20%);
       }
    }
    &:first-child a {
      border-radius: $base-border-radius 0 0 $base-border-radius;
      border-left: $base-border-darker;
    }
    &:last-child a {
      border-radius: 0 $base-border-radius $base-border-radius 0;
      border-left: 0;
    }
    a {
      &:focus {
       outline: 1px dotted $light-gray;
      }
    }
    &.active {
      a {
        background-color: $active-tab-color;
        color: $white;
      }
    }
  }
}

.tabs-default {
  @include tab-settings($base-border-darker, $action-color, $action-color)
}

.tabs-purple {
  @include tab-settings($base-border-darker, $purple, $base-font-color);
}

@mixin tabs-stacked($background-color: "") {
  li {
    display: block;
    width: 100%;
    @if $background-color != "" {
      &.active a {
      background-color: $background-color;
      }
    }
    a {
      border-bottom: $base-border-darker;
      border-top: 0;
      border-left: $base-border-darker;
      border-right: $base-border-darker;
      display: block;
      padding: $small-spacing $base-spacing;
    }
    &:first-child {
      a {
        border-radius: $base-border-radius $base-border-radius 0 0;
        border-top: $base-border-darker;
      }
    }
    &:last-child {
      a {
        border-left: $base-border-darker;
        border-top: 0;
        border-radius: 0 0 $base-border-radius $base-border-radius;
      }
    }
  }
}

@include breakpoint($small-screen-down) {
  .tabs-block-small-screen-down {
    @include tabs-stacked;
  }
}

// Tab-accordian styles
// accordion is hidden by default and tabs are shown by default
.tab-content {
  > .tab-pane {
    display: none;
    outline: none;
    width: 100%;
    &:focus {
    }
  }

  /*  @include breakpoint($tab-mode) {
      border-top: $tab-border;
      float: left;
    }
  }*/
  > .active {
    display: block;
  }
}

.tab-header .tab-link.is-active {
  border: 1px solid gainsboro;
  border-bottom-color: white;
  margin-bottom: -2px;
  border-radius: 0;
}

.panel-group {
  display: none;

  .tab-link {
    color: $teal;
    display: block;
    padding: 0.75em 1.2em;
  }

  .panel-body {
    margin: 2em .5em;
  }

  .tab-link:hover, .tab-link:focus  {
    color: $action-color;
    outline: 1px dashed $white;
    outline-offset: -3px;
  }

  a.tab-link[aria-selected="true"] {
    color: $purple;
  }

  .panel-title {
    background: $white-gray;
    border: $base-border;
    border-radius: $base-border-radius;
    //color: $tab-link-color;
    display: block;
    font-size: 1em;
    font-weight: 600;
    margin-top: .5em;
    text-decoration: none;
  }
}



.collapse {
  display: none;
  &.in {
  display: block;
  }
}


