  .hidden-medium,  .accordion-tab, .panel-group .tab-content {
    display: none !important;
  }
 .visible-medium, {
   display: block !important;
 }
}

//regular bootstrap tabs js component

.minimal-nav-tabs li {
    display: inline-block;
    padding-right: 2em;
    padding-left: 2em;
    margin-bottom: 1em;
    margin-top: 1em;
    border-right: 1px solid $action-color;
    @include breakpoint($small-screen-down) {
      padding-left: $small-spacing;
      padding-right: $small-spacing;
    }
    &.active {
      font-weight: $bold;
    }
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      border-right: 0;
    }
}

//can I use tabs stacked mixin here instead?
@include breakpoint($small-phone-screen-down) {
  ul.minimal-nav-tabs {
    border: 1px solid $light-gray;
    margin: 0 1em .75em;
    border-radius: $base-border-radius;
    li {
      border-right: 0;
      display: block;
      border-bottom: 1px solid $light-gray;
      margin: 0;
      padding: 0;
      &:first-child {
        border-radius: $base-border-radius $base-border-radius 0 0;
        a {
          border-radius: $base-border-radius $base-border-radius 0 0;
        }
      }
      &:last-child {
        border-bottom: 0;
        border-radius: 0 0 $base-border-radius $base-border-radius;
        a {
          border-radius: 0 0 $base-border-radius $base-border-radius;
        }
      }
      &.active {
        a {
          background-color: $action-color;
          color: $white;
        }
      }

      a {
        display: block;
        padding: $small-spacing $base-spacing;
      }
    }
  }
}
