//base/_buttons.scss

// Elements
//
// Styleguide 2.0

// Buttons
//
// Styleguide 2.1


// Default button
//
// Styles are not applied to button elements by default, only by adding a class
//
// markup:
// <button class="button {$modifiers}">Default Button</button>
//
// .button            - Gives default button styling
// :hover             - Slight lightening to indicate hover and focus states
// :disabled          - Dims the button to indicate it cannot be used and changes cursor to not-allowed.
//
// Styleguide 2.1.1.

.button {
  @include appearance(none);
  background-color: $action-color;
  border-radius: $base-border-radius;
  border: none;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: $base-font-family;
  font-size: $base-font-size;
  font-weight: 600;
  line-height: 1;
  padding: .8em 1em;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;

  &:hover,
  &:focus {
    background-color: tint($action-color, 14%);
    color: #fff;
  }

  &:disabled {
    cursor: not-allowed;
    background-color: tint($action-color, 55%);
  }
}

// Light button
//
// markup:
// <button class="button button-light {$modifiers}">Light Button</button>
//
// .button-light      - Modifier to make default button light colored
// :hover             - Slight lightening to indicate hover and focus states
// :disabled          - Dims the button to indicate it cannot be used and changes cursor to not-allowed.
//
// Styleguide 2.1.2.

.button {
  &-light {
    background-color: $action-color-light;
    border: 1px solid rgba(219, 137, 1, 0.25);
    color: $action-color-dark;
    &:hover,
    &:focus {
      background-color: darken($action-color-light, 4%);
      color: $action-color-dark;
    }
    &:disabled, &:disabled:hover {
      background-color: #F5F5F5;
      color: #CECDCD;
      border-color: #E3E3E3;
    }
  }
}

// Small button
//
// markup:
// <button class="button button-light button-small">Small light button</button>
//
// .button-small     - Modifier to make button smaller than default
//
// Styleguide 2.1.3.

.button-small {
  font-weight: normal;
  font-size: em(14);
  padding: .5em .75em;
}


// iOS "clickable elements" fix for role="button"
//
// Fixes "clickability" issue (and more generally, the firing of events such as focus as well)
// for traditionally non-focusable elements with role="button"
// see https://developer.mozilla.org/en-US/docs/Web/Events/click#Safari_Mobile
// Upstream patch for normalize.css submitted: https://github.com/necolas/normalize.css/pull/379 - remove this fix once that is merged

[role="button"] {
  cursor: pointer;
}

// Large button
//
// markup:
// <button class="button button-large">Large Button</button>
//
// .button-large      - Used with button class, modifies default button class by making font size larger
//
// Styleguide 2.1.4.


.button-large {
  font-size: em(20);
}

// Extra Large button
//
// markup:
// <button class="button button-x-large">X-Large Button</button>
//
// .button-x-large    - Used with button class, modifies default button class by making font size larger and increasing passing
//
// Styleguide 2.1.5.

.button-x-large {
  font-size: 1.6em;
  padding: .7em 4.6em;
}

// Centered button
//
// markup:
// <button class="button button-centered">Centered Button</button>
//
// .button-centered    - Used with button class, modifies default button class by centering it
// Styleguide 2.1.6.

.button-centered {
  display: block;
  margin: 0 auto;
}

// Full width button
//
// markup:
// <button class="button button-block">Full Width Button</button>
//
// .button-block    - Used with button class, modifies default button class
// by making it the full width of its containing element.  Can also be used with anchor tag.
//
// Styleguide 2.1.7.

.button-block {
    display: block;
    width: 100%;
}

// Unstyled buttons
//
// markup:
// <button class="unstyled-button">Close button</button>
//
// .unstyled-button - not necessary to use style on its own. With ```@extend``` statement
// removes default browser styling from ```.close, .reset-input, and .anchor-button``` classes
//
// Styleguide 2.1.8.

.unstyled-button {
  @include appearance(none);
  background: 0 0;
  border: 0;
  cursor: pointer;
  padding: 0;
  user-select: none;
}

// Style input like regular link
//
// markup:
// <button class="reset-input {$modifiers}">Button Link</button>
//
// .reset-input, .anchor-button    - Removes default button styling and gives it normal link styling.
//  Use ```.reset-input``` class for input[type="reset"] and ```.anchor-button``` class for buttons to make them look like link instead of button
// :focus          - Lightened to indicate active, focus, and hover states like regular link
//  Add `anchor-button-left` when placed to the left of another button.
//  Add `anchor-button-center-mobile` when outside of a `modal-footer` or  `page-action-footer` section and the anchor button needs to center at mobile size.
//
// Styleguide 2.1.9.
.reset-input, .anchor-button {
  @extend .unstyled-button;
  color: $action-color;
  padding: 1px 0; //otherwise bottom of font below baseline getting cut off on Safari and Firefox
  font-weight: normal;
  &:active,
  &:focus,
  &:hover {
    color: tint($action-color, 20%);
  }
  &-left {
    margin-right: 1em;
  }
  @include breakpoint($small-phone-screen-down) {
    &-center-mobile {
      display: block;
      width: 100%;
    }
  }
}

// Style two inline buttons next to each other that are outside of a modal so that they stack and center on mobile
//
// markup:
// <div class="page-action-footer">
//   <a href="roster-changes-updates.html" class="anchor-button-left">Modify this request</a>
//   <a href="roster-changes-confirmation.html" class="button">Submit request to Account Manager</a>
// </div>
//
//
// Styleguide 2.1.10.
.page-action-footer {
    float: right;
    @include breakpoint($small-phone-screen-down) {
    padding-bottom: 4em;
    position: relative;
    width: 100%;
    .button {
     display: block;
     text-align: center;
     width: 100%;
    }
    .anchor-button-left {
     bottom: -.5em;
     left: 50%;
     position: absolute;
     text-align: center;
     transform: translate(-50%, -50%);
     width: 100%;
    }
  }
}

// Make button full width and centered at mobile
//
// markup:
// <button class="button right margin-2em-top button-mobile-block">Next step: Choose date and time</button>
//
// .button-mobile-block    - for use on button or links styled as buttons
//
// Styleguide 2.1.11.
@include breakpoint($extra-small-screen-down) {
  .button-mobile-block {
      display: block;
      width: 100%;
      &+.reset-input {
        display: block;
        margin-left: 0;
        margin-top: $base-spacing;
      }
    }
   a.button-mobile-block {
      @extend .button-mobile-block;
      text-align: center;
      &+.reset-input {
        display: block;
        margin-left: 0;
        margin-top: $base-spacing;
      }
    }
}

// Turn to display block at mobile size
//
// markup:
// <input type="reset" class="reset-input reset-input-mobile-block" value="Reset Input Style">
//
// .reset-input-mobile-block    - At $mini-phone-screen-down breakpoint removes left padding and increases margin top.
//  Use when element is display inline-block to right of button element and is moved below it at small breakpoint.
//  Use in conjunction with reset-input class
//
// Styleguide 2.1.12.
input[type="reset"].reset-input-mobile-block {  //this should be changed to mobile-block-button as it is not modifying button class
  @include breakpoint($extra-small-screen-down) {
  display: block;
  margin-left: 0;
  margin-top: $base-spacing;
 }
}

// Mobile Button
//
// markup:
// <a href="/" class="mobile-button">Mobile Button</a>
//
// .mobile-button    - Add this class to links to give them the look of a button at small breakpoint ($small-screen-down)
// - Gives them a border instead of background-color
//
// Styleguide 2.1.13.
.mobile-button {
    @include breakpoint($medium-screen-down) {
    border: 2px solid $action-color;
    border-radius: $base-border-radius;
    color: $action-color;
    display: block;
    padding: .5em;
    text-align: center;

   }
}
