// elements/tables-datatables-overrides.scss

table.dataTable thead th {
 padding: 0.66667em 0.5em;
 border-bottom: 1px solid gainsboro;
}

table.dataTable thead th, table.dataTable thead td  {
  border-bottom: $base-border;
}

table.dataTable.no-footer {
  border-bottom: 0 !important;
}

#tests {
  table.dataTable thead th, table.dataTable tfoot th {
    font-weight: normal;
  }
}

table.dataTable.table-with-cell-padding tbody td {
  padding: $small-spacing 0.5em;
}

table.dataTable.table-with-cell-padding td:first-child {
  padding-left: 1em;
}

.dataTable.first-col-wrap td:first-child {
  min-width: 150px;
  white-space: normal;
}

// Datatables replace sorting icon with css triangle
%remove-backround {
  background: none;
  position: relative;
}
%position-dataTable-triangle {
  content: ' ' !important;
  position: absolute;
  top: 18px;
  right: 2px;
}

table.dataTable thead {
  .sorting {
    @extend %remove-backround;
  }
  .sorting_asc {
    @extend %remove-backround;
  }
  .sorting_desc {
    @extend %remove-backround;
  }
}

.dataTables_scrollHeadInner .dataTable thead {
  .sorting {
    &:after {
    @include triangle(12px, #BFDADB, up);
    @extend %position-dataTable-triangle;
    }
  }
  .sorting_asc {
    &::after {
    @include triangle(12px, $teal, up);
    @extend %position-dataTable-triangle;
    }
  }
  .sorting_desc {
    &::after {
    @include triangle(12px, $teal, down);
    @extend %position-dataTable-triangle;
    }
  }
}
